import * as React from 'react';
import DocumentTitle from 'react-document-title';
export const SinglePage = ({ page, appTitle }) => React.createElement("div", null,
    React.createElement(DocumentTitle, { title: `${page.title} - ${appTitle}` }),
    React.createElement(page.component, null));



