import 'babel-polyfill';
import * as React from 'react';
import { render } from 'react-dom';
import './assets/prism.css';
import { App } from './components/app';
export const SideMenuLayout = () => React.createElement("span", null, "Fork!");
function requireAll(requireContext) {
    const pages = {};
    requireContext.keys().forEach(file => {
        pages[file] = requireContext(file);
    });
    return pages;
}
export function start({ title, pages: context }) {
    const pages = requireAll(context);
    function renderApp(App) {
        const root = document.getElementById('app');
        render(React.createElement(App, { title: 'Jesper 2', pages: pages }), root);
    }
    if (window.document) {
        renderApp(App);
    }
    if (module['hot']) {
        module['hot'].accept('./components/app', () => {
            const UpdatedApp = require('./components/app').App;
            setTimeout(() => renderApp(UpdatedApp));
        });
    }
}
document.addEventListener('mouseup', event => {
    let target = event.target;
    while (target && target.tagName && target.tagName.toLowerCase() !== 'a') {
        target = target.parentElement;
    }
    if (target && target.tagName) {
        target.blur();
    }
});



